<template>
  <div>
    <!-- 搜索页面头部 -->
    <div class="search-header">
      <!-- 后退按钮 -->
      <van-icon name="arrow-left" color="white" size="0.48rem" class="goback" @click="$router.back()"/>
      <!-- 搜索组件 -->
      <van-search
      v-model.trim="kw"
      @input="inputFn"
      v-fos
      placeholder="请输入搜索关键词"
       background="#007BFF" shape="round"
       @search="searchFn" />
    </div>
    <div class="sugg-list" v-if="kw.length!==0">
        <div class="sugg-item"
        v-for="(str,index) in suggestList"
        :key="index" v-html="lightFn(str,kw)"
        @click="suggestClickFn(str)"
        ></div>
    </div>
    <!-- 搜索历史 -->
     <div class="search-history" v-else>
    <!-- 标题 -->
    <van-cell title="搜索历史">
        <!-- 使用 right-icon 插槽来自定义右侧图标 -->
        <template #right-icon>
       <van-icon name="delete" class="search-icon" @click="clearFn" />
        </template>
    </van-cell>

    <!-- 历史列表 -->
    <div class="history-list" >
        <span class="history-item" v-for="(str,index) in history" :key="index" @click="historyClickFn(str)">{{str}}</span>
    </div>
</div>
  </div>
</template>

<script>
import { suggestListAPI } from '@/api/index.js'
export default {
  data () {
    return {
      kw: '', // 搜索关键字
      time: null,
      suggestList: [],
      //   history: ['API', 'java', 'css', '前端', '后台接口', 'python']
      history: JSON.parse(localStorage.getItem('his')) || []
    }
  },
  methods: {
    inputFn () {
      clearTimeout(this.timer)
      if (this.kw.length === 0) {
        this.suggestList = []
      } else {
        this.timer = setTimeout(async () => {
          if (this.kw.length === 0) return
          const res = await suggestListAPI({
            keywords: this.kw
          })
          console.log(res)
          this.suggestList = res.data.data.options
        }, 300)
      }
    },
    lightFn (orignStr, target) {
      const reg = new RegExp(target, 'ig')
      return orignStr.replace(reg, (match) => {
        return `<span style="color:skyblue">${match}</span>`
      })
    },
    // searchFn () {
    //   if (this.kw.length > 0) {
    //     this.history.push(this.kw)
    //     setTimeout(() => {
    //       this.$router.push({
    //         path: `/search_result/${this.kw}`
    //       })
    //     }, 0)
    //   }
    // },
    // suggestClickFn (str) {
    //   this.history.push(this.str)
    //   setTimeout(() => {
    //     this.$router.push({
    //       path: `/search_result/${str}`
    //     })
    //   }, 0)
    // },
    // historyClickFn (str) {
    //   setTimeout(() => {
    //     this.$router.push({
    //       path: `/search_result/${str}`
    //     })
    //   }, 0)
    // },
    moveFn (theKw) {
      // 路由跳转传参
      // 方式1: 路径/值(前提: 路由规则:变量名), -> 接收: $route.params
      // 方式2: 路径?参数名=值  ->  接收: $route.query
      // 这2种方式, 你都可以自己在path后面路径拼接
      // 你还可以用$router.push配置项params和query让js代码内帮你拼接

      // 坑: 路由跳转, 在watch之前执行, 所以我们要让路由跳转, 来一个定时器包裹, 让跳转最后执行
      setTimeout(() => {
        this.$router.push({
          path: `/search_result/${theKw}`
        })
      }, 0)
    },
    searchFn () {
      if (this.kw.length > 0) {
        // 搜索关键字 - 保存到数组里
        this.history.push(this.kw)
        this.moveFn(this.kw)
      }
    },
    suggestClickFn (str) {
      // 搜索关键字 - 保存到数组里
      this.history.push(str)
      this.moveFn(str)
    },
    // 搜索历史-点击事件
    historyClickFn (str) {
      this.moveFn(str)
    },
    clearFn () {
      this.history = []
    }
  },

  watch: {
    history: {
      deep: true,
      handler () {
        const theSet = new Set(this.history)
        const arr = Array.from(theSet)
        localStorage.setItem('his', JSON.stringify(arr))
      }
    }
  }
}
</script>

<style scoped lang="less">
.search-header {
  height: 46px;
  display: flex;
  align-items: center;
  background-color: #007bff;
  overflow: hidden;
  /*后退按钮*/
  .goback {
    padding-left: 14px;
  }
  /*搜索组件*/
  .van-search {
    flex: 1;
  }
}
/*搜索建议列表样式 */
.sugg-list {
  .sugg-item {
    padding: 0 15px;
    border-bottom: 1px solid #f8f8f8;
    font-size: 14px;
    line-height: 50px;
    // 实现省略号的三行代码
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
/**搜索历史 */
.search-icon {
  font-size: 16px;
  line-height: inherit;
}

.history-list {
  padding: 0 10px;
  .history-item {
    display: inline-block;
    font-size: 12px;
    padding: 8px 14px;
    background-color: #efefef;
    margin: 10px 8px 0px 8px;
    border-radius: 10px;
  }
}
</style>
